<template>
  <section>
    <div class="operate">
      <div class="tips">tips：双击地图外区域，返回上一级</div>
      <div class="mapPosition">当前位置：{{ history }}</div>
    </div>
    <div id="main" ref="echartsMap">
    </div>
    <div class="desc">此方案采用的纯前端引用数据，如需展示的数据层级、json数据很多，建议数据库存储</div>
  </section>

</template>
<script>
import * as echarts from 'echarts'
import CHINAJSON from './data/china.json' //全国
import { regionData as hebeiRegionData, DATAJSON as hebei } from './data/(130000).js' //河北地图及河北全市（县区数据）
import JSON130110 from './data/130110.geojson' //石家庄-鹿泉区（镇数据）
import JSON130132 from './data/130132.geojson' //石家庄-元氏县（镇数据）
import JSON_SHANGZHUANG from './data/上庄镇.geojson' //石家庄-鹿泉区-上庄镇（村庄数据）
export default {
  data() {
    return {
      myCharts: null, //echarts图层实例
      history: [{
        name: '全国',
        level: 1
      }],
      level: 1,//层级 1全国 2省 3市 4县区 5镇 6村
      dataList: [
        { ename: "beijing", name: "北京" },
        { ename: "tianjin", name: "天津" },
        { ename: "shanghai", name: "上海" },
        { ename: "chongqing", name: "重庆" },
        { ename: "hebei", name: "河北省", dataJson: hebei },
        { ename: "henan", name: "河南省" },
        { ename: "yunnan", name: "云南" },
        { ename: "liaoning", name: "辽宁" },
        { ename: "heilongjiang", name: "黑龙江" },
        { ename: "hunan", name: "湖南" },
        { ename: "anhui", name: "安徽" },
        { ename: "shandong", name: "山东" },
        { ename: "xinjiang", name: "新疆" },
        { ename: "jiangsu", name: "江苏" },
        { ename: "zhejiang", name: "浙江" },
        { ename: "jiangxi", name: "江西" },
        { ename: "hubei", name: "湖北" },
        { ename: "guangxi", name: "广西" },
        { ename: "gansu", name: "甘肃" },
        { ename: "shanxi", name: "山西" },
        { ename: "neimenggu", name: "内蒙古" },
        { ename: "shanxi1", name: "陕西" },
        { ename: "jilin", name: "吉林" },
        { ename: "fujian", name: "福建" },
        { ename: "guizhou", name: "贵州" },
        { ename: "guangdong", name: "广东" },
        { ename: "qinghai", name: "青海" },
        { ename: "xizang", name: "西藏" },
        { ename: "sichuan", name: "四川" },
        { ename: "ningxia", name: "宁夏" },
        { ename: "hainan", name: "海南" },
        { name: "台湾" },
        { ename: "xianggang", name: "香港" },
        { ename: "aomen", name: "澳门" },
        { name: "南海诸岛" },
      ],
      // 地图配置项
      // 自定义，详细配置看echarts官网
      mapOption: {
        title: {
          text: ['{a|全国行政区}', '{b|分布}'].join(""),
          top: 75,
          left: 'center',
          textStyle: {
            fontSize: 24,
            rich: {
              a: {
                fontSize: 24,
                color: 'rgba(255,255,255,1)',
              },
              b: {
                fontSize: 24,
                fontWeight: 'lighter',
                color: 'rgba(255,255,255,1)',
              },
            },
          },
          subtextStyle: {//副标题的属性
            color: 'rgba(255,255,255,.4)',
          },
        },

        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
          },
        },
        animationDuration: 1000,
        animationEasing: 'cubicOut',
        animationDurationUpdate: 1000,
        backgroundColor: '', // 地图背景色
        geo: {
          name: 'map',
          map: 'echartsMap', // 地区名
          roam: false, //平移或缩放
          zoom: 1,
          layoutSize: '80%',
          layoutCenter: ['50%', '50%'],
          aspectScale: 0.7, //长宽比
          label: {
            show: true,
            color: 'rgba(255, 255, 255, 0.5)',
            emphasis: {
              color: '#fff',
            },
          },
          itemStyle: {
            areaColor: '#226DA4',
            borderColor: 'rgba(255, 255, 255, 0.5)',
            //立体效果展示
            shadowColor: '#6986c1',
            shadowOffsetY: 5,
            shadowOffsetX: 5,
          },

          regions: [], //对不同的区块进行着色
        },

        series: [
          {
            type: 'map',
            name: 'echartsMap',
            mapType: 'echartsMap',
            roam: false,
            selectedMode: false, //是否可以选中
            zoom: 1,
            layoutSize: '80%',
            layoutCenter: ['50%', '50%'],
            aspectScale: 0.7, //长宽比
            label: {
              show: true,
              color: '#fff',
            },
            // 图层样式
            itemStyle: {
              areaColor: '#2860a4',
              borderColor: '#6baaf0',
              borderType: 'solid',
              // shadowColor: '#6fafed',
              // shadowBlur: 15,
              borderWidth: 2,
            },

            // 选中的样式
            emphasis: {
              show: true,
              label: {
                show: true,
                color: '#fff',
              },
              itemStyle: {
                opacity: 1,
                areaColor: '#0b7571',
                borderColor: '#099799',
                borderType: 'solid',
                shadowColor: '#099799',
                shadowBlur: 15,
                borderWidth: 2,
              },
            },
            select: {
              color: '#fff',
              itemStyle: {
                areaColor: '#00c9ff',
                color: '#fff',
                borderColor: '#00deff',
                borderType: 'solid',
                shadowColor: '#5de5f3',
                shadowBlur: 2,
              },
            },
            tooltip: {
              trigger: 'item',
              backgroundColor: '#fff',
              borderColor: 'transparent',
              extraCssText: 'z-index:100;color:#fff;',
              confine: true, //是否将 tooltip 框限制在图表的区域内
              formatter: (params, ticket, callback) => {
                //根据业务自己拓展要显示的内容
                let res = ''
                // 测试数据
                const testNum = Math.floor(Math.random() * 10)
                res = `<div style="box-shadow: 0 0 10px #3BD9D9; padding: 10px; min-width:100px;position: absolute; top: 0; left:0;  border-radius: 4px; border: 1px solid #04b9ff; background: linear-gradient(to bottom,  #131d45 0%,rgba(35,90,178,.8) 100%);">
                             <div style='color:#fff; font-size: 14px;'>${params.name}</div>

                            <div style='color:#ccc;font-size: 12px;background:rgba(0,0,0.8);padding:0 6px;margin-top:5px'>${testNum
                  }</div>
                         `
                return res
              },
            },
            data: [],
          },
          {
            name: '气泡',
            type: 'effectScatter',//带闪烁
            // type: 'scatter',//不带闪烁
            coordinateSystem: 'geo',
            data: [{
              value: [],
              itemStyle: {
                color: '#fff',
                shadowBlur: 1,
                shadowColor: '#EEE'
              },
              label: { // 设置气泡文字样式
                position: 'top',
              }
            }],
            symbolSize: 8,
            encode: {
              value: 20
            },
            showEffectOn: 'render',
            rippleEffect: {
              brushType: 'stroke'
            },
            emphasis: {
              scale: true
            },
            zlevel: 1
          },
        ],
      },
    }
  },
  created() {
    /**
     * 加载地图
     */
    this.loadMap('全国')
  },
  methods: {
    loadMap(name) {
      /**
      * 注册地图
      */
      this.$nextTick(() => {
        switch (this.level) {
          case 1:
            // 初始化加载中国地图
            echarts.registerMap('echartsMap', CHINAJSON)
            break;
          case 2:
            const provinceData = this.dataList.find(item => item.name == name)
            if (!provinceData) { alert('无当前省份数据'); this.returnUpLevel(); return; }
            echarts.registerMap('echartsMap', this.dataList.find(item => item.name == name).dataJson)
            break;
          case 3:
            hebei && hebei.features.forEach((val, i) => {
              if (name.indexOf(val.properties.name) > -1) {
                echarts.registerMap('echartsMap', hebeiRegionData[`Json${val.properties.adcode}`])
              }
            })
            break;
          case 4:
            const regionEnum = {
              '鹿泉': JSON130110,
              '元氏': JSON130132
            }
            if (!regionEnum[name]) {
              alert('无当前县区数据'); this.returnUpLevel(); return;
            }
            echarts.registerMap('echartsMap', regionEnum[name])
            break;
          case 5:
            const villageEnum = {
              '上庄镇': JSON_SHANGZHUANG,
              '元氏': JSON130132
            }
            if (!villageEnum[name]) {
              alert('无当前乡镇数据'); this.returnUpLevel(); return;
            }
            echarts.registerMap('echartsMap', villageEnum[name])
            break;
          case 6:
            break;
          default:
            break;
        }
        /**
         * 请求接口数据
         */
        this.loadData(name)
        /**
         * 初始化echarts
         * 自适应尺寸
         */
        if (!this.myCharts) {
          this.myCharts = echarts.init(document.getElementById('main'))
        }
        this.$nextTick(() => {
          window.onresize = () => {
            //echarts自适应
            this.myCharts.resize()
          }
        })
        /**
         * 更新地图数据 样式等
         */
        this.updateOption()
      })
    },
    updateOption() {
      this.myCharts.setOption(this.mapOption)
      /**
       * 图例点击事件
       */
      this.myCharts.off('click')
      this.myCharts.on('click', (param) => {
        console.log('单击图例事件')
        console.log(param)

        /**
        * 选择区域高亮
        * 最后一级选择高亮
        */
        if (this.level === 5) {
          this.myCharts.dispatchAction({
            type: 'downplay',
          })
          this.myCharts.dispatchAction({
            type: 'highlight',
            name: param.name,
          })
          if (this.history.length == 6) { this.history.pop() }
          this.history.push({
            name: param.name,
            level: 6
          })
          this.loadData(param.name)
          return
        }
        this.level = this.level + 1

        // 重新注册地图（下钻）
        this.loadMap(param.name)
        this.history.push({
          name: param.name,
          level: this.level
        })
      })
      // 图例双击事件
      // 一般可用于返回上一级使用
      this.myCharts.getZr().off('dblclick')
      this.myCharts.getZr().on('dblclick', (param) => {
        // 取消所有高亮
        this.myCharts.dispatchAction({
          type: 'downplay',
        })
        this.returnUpLevel()
        // 初始化地图返回上一级 function
      })
    },
    /**
     *异步加载当前的数据 
     *个人业务 xxxxxx
     */
    loadData(name) {
      console.log('\x1B[36m%s\x1B[0m', '异步获取数据', name)
      // 测试数据
      name == '全国' && (this.mapOption.series[0].data = [{ name: '河北省', value: 222 }], this.mapOption.series[1].data = [{ name: '河北省', value: [114.39162, 38.01365] }])
      if (name == '河北省') {
        this.mapOption.series[0].data = [{ name: '石家庄市', value: 333 }]
        this.mapOption.series[1].data = [{ name: '石家庄市', value: [114.39162, 38.01365] }, { name: '保定市', value: [114.69162, 39.01365] }]
      }
      name == '石家庄市' && (this.mapOption.series[0].data = [{ name: '鹿泉', value: 4444 }], this.mapOption.series[1].data = [])
    },
    // 返回上一级
    returnUpLevel() {
      if (this.history.length == 1) { return }
      console.log('回退')
      /**
       * 层级为选中村级后，返回先取消高亮
       * 历史回退1 但是层级要改变，加载上一级的异步数据
       */
      this.myCharts.dispatchAction({
        type: 'downplay',
      })
      this.history.length !== 6 && (this.level = this.level - 1)
      this.history.pop()
      this.loadMap(this.history[this.history.length - 1].name)
    }
  }
}
</script>

<style scoped>
#main {
  background: #333;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1
}

.operate {
  position: absolute;
  z-index: 2;
  text-align: center;
  color: #fefefe;
  top: 10px;
  width: 100%;
}

.operate .tips {
  display: inline-block;
  padding: 3px 10px;
  color: #999
}

.mapPosition {
  line-height: 30px;
}

.desc {
  text-align: center;
  color: #999;
  position: absolute;
  bottom: 10px;
  width: 100%;
  z-index: 2;
}
</style>